<template lang="html">
  <div class="block block--images-rectangles" ref="blockImagesRectangle" :class="`block--images-rectangles--${index}`">
    <div class="block__image" v-for="(image, imageIndex) in block.images" :class="`block__image--${imageIndex}`">
      <img :src="image.src">
    </div>
    <div class="block__rectangle" v-for="(rectangle, rectangleIndex) in block.rectangles" :style="{height: `${calcRectHeight(rectangle)}px`}" :class="`block__rectangle--${rectangleIndex}`"></div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TimelineMax, Power3 } from 'gsap';
  import ScrollMagic from 'scrollmagic';

  export default {
    name: 'ProjectBlockImagesRectangles',
    props: {
      block: {
        type: Object,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
    computed: {
      ...mapGetters([
        'windowWidth',
      ]),
    },
    methods: {
      calcRectHeight(rectangle) {
        const isMobile = this.windowWidth < 1024;
        return (isMobile) ? rectangle.mobile.height : rectangle.height;
      },
      loadAnimations() {
        const isMobile = this.windowWidth < 1024;

        const projectCpt = this.$parent.$parent.$parent;
        const controller = projectCpt.controller;

        // Enter animation
        const timeline = new TimelineMax();
        const animDuration = 0.8;
        for (let i = 0; i < this.block.rectangles.length; i++) {
          if (isMobile) {
            timeline.to(
              `.block--images-rectangles--${this.index} .block__rectangle--${i}`, animDuration, {
                opacity: 1,
                width: this.block.rectangles[i].mobile.width,
                ease: Power3.easeInOut,
              }, `-=${animDuration - this.block.rectangles[i].delay}`
            );
          } else {
            timeline.to(
              `.block--images-rectangles--${this.index} .block__rectangle--${i}`, animDuration, {
                opacity: 1,
                width: this.block.rectangles[i].width,
                ease: Power3.easeInOut,
              }, `-=${animDuration - this.block.rectangles[i].delay}`
            );
          }
        }

        for (let i = 0; i < this.block.images.length; i++) {
          timeline.to(
            `.block--images-rectangles--${this.index} .block__image--${i}`, 0.4, {
              opacity: 1,
              scale: 1,
              webkitFilter: 'blur(0px)',
              ease: Power3.easeInOut,
            }, `-=${0.4 - this.block.images[i].delay}`
          );
        }

        new ScrollMagic.Scene({
          triggerElement: this.$refs.blockImagesRectangle,
          triggerHook: 0.6,
        })
        .setTween(timeline)
        .addTo(controller);
      },
    },
    mounted() {
      this.loadAnimations();
    },
  };
</script>

<style lang="scss">
  .block--images-rectangles {
    position: relative;

    .block__image,
    .block__rectangle {
      position: absolute;
      opacity: 0;
    }

    .block__image {
      opacity: 0;
      transform: scale(0.98);
      filter: blur(6px);
    }
    .block__image img { max-width: 100%; }
    .block__rectangle { width: 0; }
  }
</style>



