<template lang="html">
  <div class="project__description" ref="description" v-html="text"></div>
</template>

<script>
  export default {
    name: 'ProjectDescription',
    props: {
      text: {
        type: String,
        required: true,
      },
    },
  };
</script>

<style scoped lang="scss">
  @import "../../main";

  .project__description {
    color: rgba(255,255,255, .7);
    font-family: $eestiLight;
    font-size: 16px;
    line-height: 23px;
    margin-top: 56px;
    transform: translate3d(0, 6px, 0);
    opacity: 0;
    background: linear-gradient(to bottom right, white 0%, #676767 100%);
    background-position: center center;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
</style>



