<template lang="html">
  <div class="project__block" :class="`project__block--${block.type} project__block--${index}`">
    <ProjectBlockVideo v-if="block.type=='video'" :block="block" :index="index"/>
    <ProjectBlockImage v-if="block.type=='image'" :block="block" :index="index"/>
    <ProjectBlockDoubleImages v-if="block.type=='double-images'" :block="block" :index="index"/>
    <ProjectBlockDoubleVideos v-if="block.type=='double-videos'" :block="block" :index="index"/>
    <ProjectBlockImagesRectangles v-if="block.type=='images-rectangles'" :block="block" :index="index"/>
    <ProjectBlockText v-if="block.type=='text'" :block="block" :index="index"/>
  </div>
</template>

<script>
  import ProjectBlockVideo from './ProjectBlockTypes/ProjectBlockVideo';
  import ProjectBlockImage from './ProjectBlockTypes/ProjectBlockImage';
  import ProjectBlockDoubleImages from './ProjectBlockTypes/ProjectBlockDoubleImages';
  import ProjectBlockDoubleVideos from './ProjectBlockTypes/ProjectBlockDoubleVideos';
  import ProjectBlockImagesRectangles from './ProjectBlockTypes/ProjectBlockImagesRectangles';
  import ProjectBlockText from './ProjectBlockTypes/ProjectBlockText';

  export default {
    name: 'ProjectBlock',
    components: {
      ProjectBlockVideo,
      ProjectBlockImage,
      ProjectBlockDoubleImages,
      ProjectBlockDoubleVideos,
      ProjectBlockImagesRectangles,
      ProjectBlockText,
    },
    props: {
      block: {
        type: Object,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
  };
</script>



