<template lang="html">
  <video ref="video" class="c-video" :src="src" autoplay :loop="loop"></video>
</template>

<script>
  export default {
    name: 'BaseVideo',
    props: {
      src: {
        type: String,
        required: true,
      },
      loop: {
        type: Boolean,
        required: false,
      },
    },
  };
</script>

<style scoped lang="scss">
  @import "../main";

  .c-video {
    max-width: 100%;
  }
</style>



