<template lang="html">
  <div class="page page--project" :class="`c-project--${project.slug}`" ref="page">
    <BaseHeader/>
    <div class="project__ctn" v-if="project.name">
      <ProjectTitle :title="project.name" :url="project.url"/>
      <ProjectDescription :text="project.description"/>
      <ProjectFloatingBlocks :floatings="project.floatings"/>
      <ProjectBlocks :blocks="project.blocks"/>
      <ProjectNext :isAboutPage="false"/>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TweenMax, TimelineMax, Power3 } from 'gsap';
  import ScrollMagic from 'scrollmagic';
  import 'imports?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';
  import 'imports?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators';
  import config from '../config';

  import BaseHeader from '../components/BaseHeader';
  import ProjectTitle from '../components/Project/ProjectTitle';
  import ProjectDescription from '../components/Project/ProjectDescription';
  import ProjectBlocks from '../components/Project/ProjectBlocks';
  import ProjectFloatingBlocks from '../components/Project/ProjectFloatingBlocks';
  import ProjectNext from '../components/ProjectNext';

  export default {
    name: 'Project',
    components: {
      BaseHeader,
      ProjectTitle,
      ProjectDescription,
      ProjectBlocks,
      ProjectNext,
      ProjectFloatingBlocks,
    },
    computed: {
      ...mapGetters([
        'projects',
      ]),
      project() {
        const project = this.projects.filter(
          singleProject => singleProject.slug && singleProject.slug === this.$route.params.slug
        );
        return project[0];
      },
    },
    methods: {
      setPageTitle() {
        document.title = `${config.pageTitle}${this.project.name}`;
      },
      launchAnimations() {
        if (!this.project) { return false; }

        // Reset states
        TweenMax.set('.project__block', { autoAlpha: 0, y: '+=25', scale: 0.98 });
        TweenMax.set('.project__title .blur-letter', { autoAlpha: 0, filter: 'blur(20px)' });
        TweenMax.set('.project__description', { autoAlpha: 0, y: '+=6' });

        // Animations
        const titleLetters = this.project.name.split('').map(char => char);
        for (let i = 0; i < titleLetters.length; i++) {
          TweenMax.to(
            `.project__title .blur-letter--${i}`, 1, {
              autoAlpha: 1,
              filter: 'blur(0)',
              delay: Math.random() / 2,
            },
          );
        }

        const blocksClasses = this.project.blocks.map((block, index) => `.project__block--${index}`);
        const timeline = new TimelineMax();
        timeline
        .to(
          '.project__description', 1, {
            autoAlpha: 1,
            y: '-=6',
            delay: 1.2,
          },
        )
        .staggerTo(
          blocksClasses, 1, {
            autoAlpha: 1,
            y: '-=25',
            scale: 1,
            ease: Power3.easeOut,
          }, 0.12, '-=0.4'
        );

        return true;
      },
    },
    updated() {
      window.scrollTo(0, 0);
      this.controller = new ScrollMagic.Controller();
      this.launchAnimations();
    },
    beforeUpdate() {
      this.setPageTitle();
    },
    mounted() {
      window.scrollTo(0, 0);
      this.setPageTitle();
      this.launchAnimations();
    },
    beforeMount() {
      this.controller = new ScrollMagic.Controller();

      const project = this.projects.filter(
        singleProject => singleProject.slug && singleProject.slug === this.$route.params.slug
      );
      if (project.length === 0) { window.location = '/'; }
    },
  };
</script>

