<template lang="html">
  <div class="separator"></div>
</template>

<script>
  export default {
    name: 'Separator',
  };
</script>

<style scoped lang="scss">
  @import "../main";

  .separator {
    width: 0%;
    opacity: 0;
    height: 1px;
    background: rgba(255, 255, 255, .1);
    margin: 50px 0;
    animation: drawSeparatorMobile 1s forwards;
    animation-timing-function: ease-in-out;
    animation-delay: 1.3s;

    @include mq(m) {
      animation: drawSeparatorDesktop 1s forwards;
      animation-timing-function: ease-in-out;
      animation-delay: 1.3s;
    };
  }

  @keyframes drawSeparatorDesktop {
    0% {
      width: 0%;
      opacity: 0;
    }
    100% {
      width: 90vw;
      opacity: 1;
    }
  }

  @keyframes drawSeparatorMobile {
    0% {
      width: 0%;
      opacity: 0;
    }
    100% {
      width: 100%;
      opacity: 1;
    }
  }
</style>



