<template lang="html">
  <div class="block block--text" :class="`block--text--${index}`">
    <div class="text" v-html="block.text"></div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TweenMax, Power1 } from 'gsap';
  import ScrollMagic from 'scrollmagic';

  export default {
    name: 'ProjectBlockText',
    props: {
      block: {
        type: Object,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
    computed: {
      ...mapGetters([
        'windowHeight',
      ]),
    },
    methods: {
      launchAnimations() {
        // Reset states
        TweenMax.set('.block--text .text li', { autoAlpha: 0, x: '-=10', filter: 'blur(2px)' });

        const projectCpt = this.$parent.$parent.$parent;
        const controller = projectCpt.controller;

        const tween = TweenMax.staggerTo(
          `.block--text--${this.index} .text li`, 0.4,
          {
            autoAlpha: 1,
            x: '+=10',
            webkitFilter: 'blur(0)',
            ease: Power1.easeInOut,
            delay: 0.12,
          },
          0.08
        );

        // Parallax animation
        new ScrollMagic.Scene({
          triggerElement: `.block--text--${this.index}`,
          triggerHook: 0.6,
        })
        .setTween(tween)
        .addTo(controller);
      },
    },
    updated() {
      this.launchAnimations();
    },
    mounted() {
      this.launchAnimations();
    },
  };
</script>

<style lang="scss">
  @import "../../../main";

  .block--text {
    .text {
      color: $white;
      font-size: 16px;
      line-height: 26px;
      font-family: $eestiLight;
      opacity: 0.9;

      ul li {
        list-style-type: none;
        padding: 0;
        margin: 0;
        opacity: 0;
        transform: translate3d(-10px, 0, 0);
        filter: blur(2px);

        a, span { color: #8f8f8f; text-decoration: none; }

        &.agency { margin-bottom: 22px; }
      }
    }
  }
</style>



