<template lang="html">
  <section class="block block--double-videos" :class="`block--double-videos--${index}`" ref="blockDoubleVideos">
    <div class="block__video" v-for="(video, videoIndex) in block.videos">
      <video :src="`${video.src}`" autoplay :loop="`${video.loop}`" :class="`video video--${videoIndex}`"></video>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'ProjectBlockDoubleVideos',
    props: {
      block: {
        type: Object,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
  };
</script>

<style lang="scss">
</style>



