<template lang="html">
  <span ref="letter" class="link__letter" :style="computedStyle">{{letter}}</span>
</template>

<script>
  import { mapGetters } from 'vuex';

  export default {
    name: 'menuLetter',
    props: {
      letter: {
        type: String,
        required: true,
      },
      projectSlug: {
        type: String,
        required: true,
      },
    },
    data() {
      return {
        letterPos: { x: 0, y: 0 },
      };
    },
    computed: {
      ...mapGetters([
        'animatedMouseX',
        'animatedMouseY',
        'windowWidth',
        'activeMenuItem',
      ]),
      computedStyle() {
        const xValue = this.animatedMouseX - this.letterPos.x;
        const squareXValue = xValue * xValue;
        const yValue = this.animatedMouseY - this.letterPos.y;
        const squareYValue = yValue * yValue;
        const distance = Math.sqrt(squareXValue + squareYValue);

        let opacity = (0.7 - (distance / 1000) > 0) ? 0.7 - (distance / 1000) : 0;
        // No mouse effect on mobile
        if (this.windowWidth < 550) { opacity = 0.7; }
        if (this.projectSlug === this.activeMenuItem) { opacity = 1; }
        return `opacity: ${0.3 + (opacity)}; transition: all ease .2s;`;
      },
    },
    mounted() {
      const position = this.$el.getBoundingClientRect();
      this.letterPos = {
        x: position.left,
        y: position.top,
      };
    },
  };
</script>



