<template lang="html">
  <p class="credits">Developped by <a target="_blank" href="http://romainbillard.fr">Romain Billard</a></p>
</template>

<script>
  export default {
    name: 'Credits',
  };
</script>

<style scoped lang="scss">
  @import "../../main";

  .credits {
    margin-top: 60px;
    text-align: center;
    font-family: $eestiLight;
    font-size: 14px;
    color: rgba(255, 255, 255, .3);
    opacity: 0;
    transform: translate3d(0, -6px, 0);
    animation: creditsEnter .6s forwards;
    animation-delay: 2.2s;

    @include mq(m) {
      margin-top: 100px;
      text-align: left;
    };

    a {
      color: rgba(255, 255, 255, .3);
      text-decoration: none;
    }
  }

  @keyframes creditsEnter {
    0% {
      opacity: 0;
      transform: translate3d(0, -6px, 0);
    }
    100% {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
</style>



