<template lang="html">
  <div class="project__blocks">
    <ProjectBlock v-for="(block, blockIndex) in blocks" :block="block" :index="blockIndex"/>
  </div>
</template>

<script>
  import ProjectBlock from './ProjectBlock';

  export default {
    name: 'ProjectBlocks',
    components: {
      ProjectBlock,
    },
    props: {
      blocks: {
        type: Array,
        required: true,
      },
    },
  };
</script>

<style scoped lang="scss">
  @import "../../main";
</style>



