<template lang="html">
  <div class="animated-rectangle" ref="animatedRectangle" :style="computedStyle"></div>
</template>

<script>
  import { TweenMax, TimelineMax, Power2 } from 'gsap';

  export default {
    name: 'AnimatedRectangle',
    props: {
      direction: {
        type: String,
        required: true,
      },
      height: {
        type: Number,
        required: true,
      },
      width: {
        type: Number,
        required: true,
      },
      delay: {
        type: Number,
        required: true,
      },
    },
    computed: {
      computedStyle() {
        return { height: `${this.height}px`, width: `${this.width}px` };
      },
    },
    methods: {
      verticalAnimation() {
        TweenMax.set(this.$refs.animatedRectangle, { height: 0, autoAlpha: 0 });
        const timeline = new TimelineMax();
        timeline
        .delay(this.delay)
        .to(
          this.$refs.animatedRectangle, 1, {
            autoAlpha: 1,
            height: this.height,
            ease: Power2.easeInOut,
          }
        );
      },
      horizontalAnimation() {
        console.log('horiz...');
      },
    },
    mounted() {
      if (this.direction === 'vertical') {
        this.verticalAnimation();
      } else {
        this.horizontalAnimation();
      }
    },
  };
</script>

<style scoped lang="scss">
  .animated-rectangle {
    background: rgba(255,255,255, .1);
  }
</style>



