<template lang="html">
  <div class="social-links">
    <ul>
      <li><a target="_blank" href="https://twitter.com/JulienLz">Twitter</a><span class="item-separator"></span></li>
      <li><a target="_blank" href="https://fr.linkedin.com/in/julien-lozingue-6371a0b0">LinkedIn</a><span class="item-separator"></span></li>
      <li><a target="_blank" href="https://www.behance.net/Julienlozingue">Behance</a><span class="item-separator"></span></li>
      <li><a target="_blank" href="https://dribbble.com/julienwz">Dribbble</a><span class="item-separator"></span></li>
      <li><a target="_blank" href="https://fr.pinterest.com/jlozingue/">Pinterest</a><span class="item-separator"></span></li>
      <li><a href="mailto:julienlozingue@gmail.com">Contact</a></li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'SocialLinks',
  };
</script>

<style scoped lang="scss">
  @import "../../main";

  .social-links {
    ul {
      display: flex;
      list-style-type: none;
      flex-wrap: wrap;

      li {
        display: flex;
        width: 33%;
        margin-bottom: 40px;
        position: relative;

        &:nth-child(1),
        &:nth-child(4) {
          justify-content: flex-start;
        }

        &:nth-child(2),
        &:nth-child(5) {
          justify-content: center;

          &:before,
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            width: 34px;
            height: 1px;
            background-color: rgba(255, 255, 255, .1);
            opacity: 0;
            animation: enterSocialSeparators 2s forwards;
            animation-delay: 1.4s;

            @include mq(m) { content: none; };
          }

          &:after {
            right: -20px;
            transform: translate3d(50%, 0, 0);
          }

          &:before {
            left: -20px;
            transform: translate3d(-50%, 0, 0);
          }
        }

        &:nth-child(3),
        &:nth-child(6) {
          justify-content: flex-end;
        }

        @include mq(m) {
          width: auto;
          margin-right: 20px;
          margin-bottom: 0px;
          justify-content: flex-start;
        };

        a {
          color: rgba(255, 255, 255, .5);
          font-size: 14px;
          font-family: $eestiLight;
          text-decoration: none;
          transition: $defaultTransition;
          animation: linkEnterAnimation .5s forwards;
          transform: translate3d(-6px, 0, 0);
          opacity: 0;

          &:hover {
            color: rgba(255, 255, 255, .9);
          }
        }

        .item-separator {
          display: none;

          @include mq(m) {
            display: block;
            align-self: center;
            width: 20px;
            height: 1px;
            background: rgba(255, 255, 255, .2);
            margin-left: 20px;
            opacity: 0;
            transform: translate3d(-6px, 0, 0);
            animation: separatorEnterAnimation .5s forwards;
          };
        }

        @for $i from 1 through 6 {
          &:nth-child(#{$i}) {
            a {
              animation-delay: 1.3+($i*0.12)+s;
            }
            .item-separator {
              animation-delay: 1.3+($i*0.12)+s;
            }
          }
        }
      }
    }
  }

  @keyframes linkEnterAnimation {
    0% {
      opacity: 0;
      transform: translate3d(-6px, 0, 0);
    }
    100% {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }

  @keyframes separatorEnterAnimation {
    0% {
      opacity: 0;
      transform: translate3d(-6px, 0, 0);
    }
    100% {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }

  @keyframes enterSocialSeparators {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
</style>



