<template lang="html">
  <div class="about__biography" ref="biography">
    <p>My name is Julien Lozingue, French designer based in Paris and currently in 4th year at HETIC. <br>Formerly part of <a target="_blank" href="https://www.sweetpunk.com/agence">SweetPunk</a> and the beer-loving folks from <a target="_blank" href="http://www.dogstudio.be/">Dogstudio</a>,</p>
    <p>I’m now looking for a 6th months journey.</p>
    <p>I’m also available for freelance work, don’t hesite to contact me if needed</p>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';

  export default {
    name: 'Biography',
    data() {
      return {
        rectCenter: {
          x: 0,
          y: 0,
        },
      };
    },
    computed: {
      ...mapGetters([
        'mousePosition',
        'windowWidth',
        'windowHeight',
      ]),
      computedStyle() {
        const arg1 = this.mousePosition.y - this.rectCenter.y;
        const arg2 = this.mousePosition.x - this.rectCenter.x;
        const angle = (Math.atan2(arg1, arg2) * 180) / Math.PI;
        return {
          background: `linear-gradient(${angle}deg, #070707, #ffffff)`,
          '-webkit-background-clip': 'text',
          '-webkit-text-fill-color': 'transparent',
        };
      },
    },
    mounted() {
      const elemHeight = this.$el.clientHeight;
      const elemWidth = this.$el.clientWidth;
      const elemPosition = this.$el.getBoundingClientRect();
      this.rectCenter = {
        x: elemPosition.left + (elemWidth / 2),
        y: elemPosition.top + (elemHeight / 2),
      };
    },
  };
</script>

<style scoped lang="scss">
  @import "../../main";

  .about__biography {
    max-width: 790px;
    background: linear-gradient(321deg, #070707, #ffffff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 0;
    transform: translate3d(0, 6px, 0);
    animation: biographyEnter .6s forwards;
    animation-delay: 0.8s;

    p {
      font-family: $butlerRegular;
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 18px;

      @include mq(m) {
        font-size: 18px;
        line-height: 28px;
      };

      &:last-child { margin-bottom: 0; }
    }
  }

  @keyframes biographyEnter {
    0% {
      opacity: 0;
      transform: translate3d(0, 6px, 0);
    }
    100% {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
</style>



