<template lang="html">
  <a :href="url" target="_blank" class="project__title">
    <BlurLetter v-for="(letter, letterIndex) in title.split('')" :letter="letter" :blur="14" :index="letterIndex" />
    <div class="title__mobile">
      <div class="title__word" v-for="(word, wordIndex) in title.split('-')">
        <BlurLetter v-for="(letter, letterIndex) in word.split('')" :letter="letter" :blur="14" :index="letterIndex" />
      </div>
    </div>
  </a>
</template>

<script>
  import BlurLetter from './BlurLetter';

  export default {
    name: 'ProjectTitle',
    components: {
      BlurLetter,
    },
    props: {
      title: {
        type: String,
        required: true,
      },
      url: {
        type: String,
        required: true,
      },
    },
  };
</script>

<style lang="scss">
  @import "../../main";

  .project__title {
    color: white;
    font-family: $butlerRegular, serif;
    text-transform: uppercase;
    font-size: 40px;
    line-height: 64px;
    letter-spacing: .2em;

    @include mq(m) { letter-spacing: 41.8px; }

    .blur-letter {
      color: white;
      display: inline-block;
      opacity: 0;
      filter: blur(20px);
    }

    > .blur-letter {
      display: none;

      @include mq(m) {
        display: inline-block;
      };
    }

    .title__mobile {
      font-size: 34px;

      @include mq(m) { display: none; };
    }
  }
</style>



