<template lang="html">
  <div class="project__next" @click.prevent="goToNext" :class="{'project__next--about': isAboutPage}">
    <div class="next__link">
      <div class="link__letter" v-for="(letter, letterIndex) in nextProject.name.split('')">
        <span>{{letter}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  import ScrollMagic from 'scrollmagic';
  import { TweenMax, TimelineMax, Power1 } from 'gsap';
  import { mapGetters } from 'vuex';

  export default {
    name: 'ProjectNext',
    props: {
      isAboutPage: {
        type: Boolean,
        required: true,
      },
    },
    computed: {
      ...mapGetters([
        'projects',
      ]),
      nextProject() {
        if (this.isAboutPage) { return { name: 'About' }; }
        let currentProject = this.projects.filter(
          singleProject => singleProject.slug === this.$route.params.slug
        );
        currentProject = currentProject[0];
        return currentProject.next;
      },
    },
    methods: {
      goToNext() {
        if (this.isAboutPage) { return false; }
        const appCpt = this.$parent.$parent;
        appCpt.leaveAnimations(() => {
          this.$router.push(`/${this.nextProject.slug}`);
        });
        return true;
      },
      defaultEnterAnimations() {
        const projectCpt = this.$parent;
        const controller = projectCpt.controller;
        // Enter animation
        TweenMax.set('.project__next .link__letter', { autoAlpha: 0 });
        const tl = new TimelineMax();
        const tween = tl.staggerTo(
          '.project__next .link__letter', 0.5, {
            autoAlpha: 1,
            ease: Power1.easeInOut,
          }, 0.06
        );
        new ScrollMagic.Scene({
          triggerElement: '.project__next',
          triggerHook: 1,
        })
        .setTween(tween)
        .addTo(controller);
      },
      aboutPageEnterAnimations() {
        TweenMax.set('.project__next .link__letter', { autoAlpha: 0 });
        const tl = new TimelineMax();
        tl.delay(2.4)
        .staggerTo(
          '.project__next .link__letter', 0.5, {
            autoAlpha: 1,
            ease: Power1.easeInOut,
          },
          0.06
        );
      },
      enterAnimations() {
        if (this.isAboutPage) {
          this.aboutPageEnterAnimations();
        } else {
          this.defaultEnterAnimations();
        }
      },
    },
    updated() {
      this.enterAnimations();
    },
    beforeUpdate() {
      TweenMax.set('.link__letter', { autoAlpha: 0 });
    },
    mounted() {
      this.enterAnimations();
    },
  };
</script>

<style scoped lang="scss">
  @import "../main";

  .project__next {
    width: 100%;
    display: flex;
    cursor: pointer;
    overflow: hidden;
    justify-content: center;
    user-select: none;
    margin-top: 300px;
    height: 40px;

    @include mq(m) {
      height: 50px;
      margin-top: 400px;
    };

    .next__link {
      display: flex;
      justify-content: space-between;
      width: 100%;
      font-family: $butlerRegular, serif;
      text-transform: uppercase;
      text-decoration: none;
      text-align: center;
      color: #dbdbdb;
      font-size: 24px;
      transform: translate3d(0, 18px, 0);
      transition: $defaultTransition;

      @include mq(m) {
        font-size: 40px;
        letter-spacing: 40px;
        transform: translate3d(0, 20px, 0);
      };

      .link__letter {
        display: flex;

        span {
          display: inline-block;
          margin: 0 auto;
          width: auto;
          color: rgba(255, 255, 255, .5);
          transition: $defaultTransition;

          &:hover { color: rgba(255, 255, 255, 1); }
        }
      }
    }

    &.project__next--about {
      cursor: default;
      display: none;

      @include mq(m) { display: block; };

      .next__link {
        .link__letter {
          &:hover { opacity: .5 !important; }
        }
      }
    }
  }
</style>



