<template>
  <div class="page page--homepage">
    <section class="section">
      <MainMenu/>
    </section>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import config from '../config';

  import MainMenu from '../components/MainMenu';

  export default {
    name: 'home',
    components: {
      MainMenu,
    },
    computed: {
      ...mapGetters([
        'windowWidth',
      ]),
    },
    methods: {
      handleResize() {
        const newWidth = window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
        const newHeight = window.innerHeight ||
          document.documentElement.clientHeight ||
          document.body.clientHeight;
        this.$store.dispatch('moveMouse', { x: newWidth / 2, y: newHeight / 2 });
      },
    },
    mounted() {
      document.title = 'Julien Lozingue';
      window.addEventListener('resize', () => this.handleResize());
    },
  };
</script>

<style lang="scss">
  @import "../main";

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body { background: #000; }

  .page--homepage {
    font-family: $butlerRegular, serif;
    justify-content: center;
    display: flex;
    height: 100vh;

    .section {
      width: 80%;
      margin: 0 auto;
      align-self: center;

      @include mq(m) {
        max-width: 915px;
      }
    }
  }
</style>



