<template lang="html">
  <header class="c-header">
    <a href="#" class="header__previous" @click.prevent="handlePreviousClick">
      <BackArrowIcon/>
    </a>
  </header>
</template>

<script>
  import BackArrowIcon from './Icons/BackArrowIcon';

  export default {
    name: 'Header',
    components: {
      BackArrowIcon,
    },
    methods: {
      handlePreviousClick() {
        const rootCpt = this.$root.$children[0];
        rootCpt.leaveAnimations(() => {
          this.$router.push('/');
        });
      },
    },
  };
</script>

<style lang="scss">
  @import "../main";

  .c-header {
    opacity: 0;
    padding: $containerPadding;
    padding-bottom: 0;
    animation: arrowEnter .3s forwards;
    animation-delay: .2s;

    @include mq(m) {
      position: fixed;
    };

    .header__previous {
      svg {
        height: 30px;
        height: 13px;
        transition: $defaultTransition;
      }
      svg * { fill: white; }

      &:hover {
        svg { transform: translate3d(-2px, 0, 0); }
      }
    }
  }

  @keyframes arrowEnter {
    0% {
      opacity: 0;
      transform: translate3d(-2px, 0, 0);
    }
    100% {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }
</style>



