<template lang="html">
  <span class="blur-letter" :style="style" :class="`blur-letter--${index}`">
    {{letter==" " ? '&nbsp;' : letter}}
  </span>
</template>

<script>
  export default {
    name: 'BlurLetter',
    props: {
      letter: {
        type: String,
        required: true,
      },
      blur: {
        type: Number,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
    computed: {
      style() {
        return { filter: `blur(${this.blur}px)` };
      },
    },
  };
</script>



