<template lang="html">
  <div class="block block--image" ref="blockImage">
    <img :src="block.src">
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TweenMax, Power1 } from 'gsap';
  import ScrollMagic from 'scrollmagic';
  import 'imports?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';
  import 'imports?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators';

  export default {
    name: 'ProjectBlockImage',
    props: {
      block: {
        type: Object,
        required: true,
      },
    },
    computed: {
      ...mapGetters([
        'windowHeight',
      ]),
    },
    methods: {
      loadAnimations() {
        // Reset states
        TweenMax.set(this.$refs.blockImage, { autoAlpha: 0, scale: 0.98, y: '+=35' });

        const projectCpt = this.$parent.$parent.$parent;
        const controller = projectCpt.controller;

        // Enter animation
        const tween = new TweenMax(
          this.$refs.blockImage, 0.6, {
            autoAlpha: 1,
            scale: 1,
            y: '-=35',
            webkitFilter: 'blur(0px)',
            ease: Power1.easeInOut,
          },
        );
        new ScrollMagic.Scene({
          triggerElement: this.$refs.blockImage,
          triggerHook: 0.6,
        })
        .setTween(tween)
        .addTo(controller);
      },
    },
    mounted() {
      this.loadAnimations();
    },
  };
</script>

<style scoped lang="scss">
  .block--image {
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: translate3d(0, 35px, 0) scale(0.98);
    filter: blur(6px);

    img { max-width: 100%; }
  }
</style>



