<template lang="html">
  <div class="page page--about" ref="page">
    <BaseHeader/>
    <div class="about__ctn">
      <div class="about__content">
        <AnimatedRectangle :height="60" :width="20" :delay="1.2" direction="vertical"/>
        <Biography/>
        <Separator/>
        <SocialLinks/>
        <Credits/>
      </div>
      <div class="about__picture about__picture--desktop"></div>
      <div class="about__picture about__picture--mobile">
        <img class="desktop" src="/static/images/about/picture-mobile.png" alt="Julien Lozingue, French designer">
      </div>
    </div>
    <ProjectNext :isAboutPage="true"/>
  </div>
</template>

<script>
  import BaseHeader from 'components/BaseHeader';
  import Biography from 'components/About/Biography';
  import Separator from 'components/Separator';
  import SocialLinks from 'components/About/SocialLinks';
  import Credits from 'components/About/Credits';
  import AnimatedRectangle from 'components/AnimatedRectangle';
  import ProjectNext from 'components/ProjectNext';
  import config from '../config';

  export default {
    name: 'About',
    components: {
      BaseHeader,
      Biography,
      Separator,
      SocialLinks,
      Credits,
      AnimatedRectangle,
      ProjectNext,
    },
    mounted() {
      document.title = `${config.pageTitle}About`;
    },
  };
</script>

<style lang="scss">
  @import "../main";

  .page--about {
    position: relative;
    overflow: hidden;

    .about__ctn {
      display: flex;
      min-height: calc(100vh - 75px);
      padding: $defaultPadding;
      margin: 0 auto;
      color: $white;

      @include mq(m) {
        max-width: 1024px;
      };

      .about__content {
        display: flex;
        flex-direction: column;
        align-self: center;
        width: 100%;

        @include mq(m) {
          position: relative;
        };

        .animated-rectangle {
          position: absolute;
          left: -30px;
          top: 4px;
        }
      }

      .about__picture {
        &--desktop {
          display: none;
          position: absolute;
          margin-top: 20px;
          top: 50%;
          right: 4vw;
          width: 530px;
          height: 594px;
          transform: translate3d(0, -50%, 0);
          opacity: 0;
          animation: pictureEnter 1.2s forwards;
          animation-delay: .8s;
          background-color: black;
          background-image: url('/static/images/about/picture.png');
          background-blend-mode: hard-light;
          z-index: -1;

          @include mq(m) {
            display: block;
          };
        }

        &--mobile {
          opacity: 0.2;
          mix-blend-mode: color-dodge;
          position: absolute;
          top: 20px;
          left: 50%;
          width: 70vw;
          transform: translate3d(-50%, 0, 0);

          img { max-width: 100%; }

          @include mq(m) {
            display: none;
          };
        }
      }
    }

    .project__next {
      position: fixed;
      bottom: 0;
      left: 50%;
      max-width: 1024px;
      transform: translate3d(-50%, 0, 0);
    }

    &:after {
      content: '';
      background: url('/static/images/noise-background.png');
      animation: noiseEnter 1s forwards, noiseMove .3s infinite;
      position: fixed;
      pointer-events: none;
      z-index: 20;
      top: 0;
      left: 0;
      width: 100vw;
      height: 100vh;
      opacity: 0;
    }
  }

  @keyframes noiseMove {
    0%, 100% { background-position: center center; }
    50% { background-position: 200px 200px; }
  }

  @keyframes noiseEnter {
    0% { opacity: 0; }
    100% { opacity: .8; }
  }

  @keyframes pictureEnter {
    0% { opacity: 0; }
    100% { opacity: .4; }
  }
</style>



