<template lang="html">
  <ul class="menu">
    <li v-for="(project, projectIndex) in allLinks" class="menu__item" :class="`item--${project.slug}`">
      <a href="#" class="item__link" @click.prevent="handleItemClick(project)" @mouseenter="itemMouseEnter(project)" @mouseleave="itemMouseLeave()">
        <div class="link__letters">
          <div class="letter-ctn" v-for="(letter, letterIndex) in project.name.split('')" :class="`letter-${projectIndex}-${letterIndex}`">
            <MenuLetter :letter="letter" :projectSlug="project.slug"/>
          </div>
        </div>
      </a>
    </li>
  </ul>
</template>

<script>
  import { TimelineMax, Power1, Power0 } from 'gsap';
  import { mapGetters } from 'vuex';

  import MenuLetter from './MenuLetter';

  export default {
    name: 'mainMenu',
    components: {
      MenuLetter,
    },
    data() {
      return {
        randomSplittedNames: [[], [], [], [], [], [], [], []],
      };
    },
    computed: {
      ...mapGetters([
        'projects',
        'links',
      ]),
      namesChars() {
        return this.allLinks.map(item => item.name.split(''));
      },
      allLinks() {
        return this.projects.concat(this.links);
      },
    },
    methods: {
      handleItemClick(item) {
        const filteredProjects = this.projects.filter(singleProject => singleProject === item);
        const isProject = filteredProjects.length > 0;
        if (isProject) {
          this.leaveAnimations(() => {
            this.$router.replace(`/project/${item.slug}`);
          });
        } else {
          this.leaveAnimations(() => {
            this.$router.replace(`/${item.slug}`);
          });
        }
      },
      itemMouseEnter(item) {
        this.$store.dispatch('setActiveMenuItem', item.slug);
      },
      itemMouseLeave() {
        this.$store.dispatch('setActiveMenuItem', null);
      },
      leaveAnimations(callback) {
        for (let i = 0; i < this.namesChars.length; i++) {
          for (let j = 0; j < this.namesChars[i].length; j++) {
            const randomIndex = Math.round(Math.random() * 7);
            this.randomSplittedNames[randomIndex].push(`.letter-${i}-${j}`);
          }
        }

        const tl = new TimelineMax({ onComplete: () => { callback(); } });
        const anim = {
          opacity: 0,
          webkitFilter: 'blur(20px)',
          ease: Power1.easeInOut,
        };
        for (let k = 0; k < this.randomSplittedNames.length; k++) {
          tl.to(this.randomSplittedNames[k], 0.6, anim, '-=0.56');
        }
      },
      enterAnimations() {
        for (let i = 0; i < this.namesChars.length; i++) {
          for (let j = 0; j < this.namesChars[i].length; j++) {
            const randomIndex = Math.round(Math.random() * 7);
            this.randomSplittedNames[randomIndex].push(`.letter-${i}-${j}`);
          }
        }

        const tl = new TimelineMax();
        tl.delay(0.4);
        for (let k = 0; k < this.randomSplittedNames.length; k++) {
          tl.to(
            this.randomSplittedNames[k], 1,
            {
              opacity: 0.7,
              webkitFilter: 'blur(0px)',
              ease: Power0.easeOut,
            },
            '-=0.9'
          );
        }
      },
    },
    mounted() {
      this.$store.dispatch('setActiveMenuItem', null);
      this.enterAnimations();
    },
  };
</script>

<style scoped lang="scss">
  @import "../main";

  .menu {
    &__item {
      display: flex;
      text-transform: uppercase;

      .item__link {
        width: 100%;
        color: $white;
        font-size: 22px;
        text-decoration: none;
        padding: 4vh 0;

        @include mq(m) {
          font-size: 38px;
          padding: 3vh 0;
        }

        .link__letters {
          width: 100%;
          display: flex;
          align-self: center;
          justify-content: space-between;

          .letter-ctn {
            opacity: 0;
            filter: blur($defaultBlur);
          }
        }
      }
    }

    .item--about {
      .item__link {
        .letter-ctn {
          position: relative;

          &:after {
            content: '';
            position: absolute;
            display: inline-block;
            background-color: #FFF;
            top: 50%;
            height: 10px;
            width: 0;
            opacity: 0;
            margin-left: 3.5vw;
            transform: translate3d(0, -50%, 0);
            animation: separatorEnterMobile .25s forwards;

            @include mq(m) {
              height: 20px;
              animation: separatorEnterDesktop .33s forwards;
              animation-timing-function: ease-out;
            };
          }

          &:last-child {
            &:after { content: none; }
          }

          &:first-child:after { animation-delay: 1s; }
          &:nth-child(2):after { animation-delay: 1.16s; }
          &:nth-child(3):after { animation-delay: 1.32s; }
          &:nth-child(4):after { animation-delay: 1.48s; }
        }
      }
    }
  }

  @keyframes separatorEnterMobile {
    0% {
      width: 0;
      opacity: 0;
    }
    100% {
      width: 24px;
      opacity: 0.1;
    }
  }
  @keyframes separatorEnterDesktop {
    0% {
      width: 0;
      opacity: 0;
      transform: translate3d(-16px, -50%, 0);
    }
    100% {
      width: 60px;
      opacity: 0.1;
      transform: translate3d(0, -50%, 0);
    }
  }
</style>



