<template lang="html">
  <div class="block block--floating" :class="`block--floating--${index}`" ref="blockFloating">
    <img :src="block.src">
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TweenMax } from 'gsap';
  import ScrollMagic from 'scrollmagic';

  export default {
    name: 'ProjectFloatingBlock',
    props: {
      block: {
        type: Object,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
    computed: {
      ...mapGetters([
        'windowHeight',
      ]),
    },
    mounted() {
      // Reset states
      TweenMax.set(this.$refs.blockFloating, { y: 0, autoAlpha: 0 });

      const projectCpt = this.$parent.$parent;
      const controller = projectCpt.controller;

      // Enter animation
      const tween = new TweenMax(this.$refs.blockFloating, 1, { y: `-=${this.block.scrollLength}` });
      const alphaTween = new TweenMax(this.$refs.blockFloating, 1, { autoAlpha: 1 });
      new ScrollMagic.Scene({
        triggerElement: this.$refs.blockFloating,
        triggerHook: 1,
        duration: this.windowHeight * 2,
      })
      .setTween(tween)
      .addTo(controller);

      new ScrollMagic.Scene({
        triggerElement: this.$refs.blockFloating,
        triggerHook: 0.7,
        duration: this.windowHeight / 3,
      })
      .setTween(alphaTween)
      .addTo(controller);
    },
  };
</script>

<style lang="scss">
</style>



