<template lang="html">
  <section class="block block--double-images" :class="`block--double-images--${index}`" ref="blockDoubleImages">
    <div class="block__image" v-for="(image, imageIndex) in block.images">
      <img :src="image.src" :class="`image image--${imageIndex}`">
    </div>
  </section>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TweenMax, Power1 } from 'gsap';
  import ScrollMagic from 'scrollmagic';
  import 'imports?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';
  import 'imports?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators';

  export default {
    name: 'ProjectBlockDoubleImages',
    props: {
      block: {
        type: Object,
        required: true,
      },
      index: {
        type: Number,
        required: true,
      },
    },
    computed: {
      ...mapGetters([
        'windowHeight',
      ]),
    },
    methods: {
      loadAnimations() {
        // Reset state
        TweenMax.set('.block--double-images .block__image', { autoAlpha: 0, scale: 0.98, y: '+=35' });

        const projectCpt = this.$parent.$parent.$parent;
        const controller = projectCpt.controller;

        // Enter animation
        const tween = TweenMax.staggerTo(
          `.block--double-images--${this.index} .block__image`, 0.6, {
            autoAlpha: 1,
            scale: 1,
            webkitFilter: 'blur(0px)',
            y: '-=35',
            ease: Power1.easeInOut,
          }, 0.1
        );
        new ScrollMagic.Scene({
          triggerElement: this.$refs.blockDoubleImages,
          triggerHook: 0.6,
        })
        .setTween(tween)
        .addTo(controller);
      },
    },
    mounted() {
      this.loadAnimations();
    },
  };
</script>

<style scoped lang="scss">
  .block--double-images {
    position: relative;

    .block__image {
      position: absolute;
      opacity: 0;
      transform: translate3d(0, 35px, 0) scale(0.98);
      filter: blur(6px);

      img { max-width: 100%; }
    }
  }
</style>



