<template lang="html">
  <div class="project__floatings" ref="floatings">
    <div class="floatings__ctn">
      <ProjectFloatingBlock v-for="(block, blockIndex) in floatings" :block="block" :index="blockIndex"/>
    </div>
  </div>
</template>

<script>
  import { TweenMax, Power2 } from 'gsap';

  import ProjectFloatingBlock from './ProjectBlockTypes/ProjectFloatingBlock';

  export default {
    name: 'ProjectFloatingBlocks',
    components: {
      ProjectFloatingBlock,
    },
    props: {
      floatings: {
        type: Array,
        required: true,
      },
    },
    methods: {
      enterAnimations() {
        TweenMax.set(this.$refs.floatings, { autoAlpha: 0 });
        TweenMax.to(
          this.$refs.floatings, 2, {
            autoAlpha: 1,
            ease: Power2.easeInOut,
            delay: 1.4,
          },
        );
      },
    },
    updated() {
      this.enterAnimations();
    },
    mounted() {
      this.enterAnimations();
    },
  };
</script>

<style lang="scss">
</style>



